/* eslint-disable react/prop-types */

import React, { useState, useRef } from 'react';
import ButtonText from '../../button-text';
import Col from '../../col';
import Grid from '../../grid';
import Icon from '../../icon';
import Radio from '../../radio';
import RadioErrorMessage from '../../radio-error-message';
import RadioGroup from '../../radio-group';
import RadioGroupControl from '../../radio-group-control';
import RadioGroupLegend from '../../radio-group-legend';
import RadioList from '../../radio-list';
import RadioListItem from '../../radio-list-item';
import Row from '../../row';
import Typography from '../../typography';

const RadioExample = () => {
  const refOneValidation = useRef();
  const refTwoValidation = useRef();
  const refThreeValidation = useRef();
  const [showError, setShowError] = useState(false);

  const [stateOne, setStateOne] = useState({
    selectedValue: '1'
  });

  const [stateThree, setStateThree] = useState({
    pizzas: [
      {
        checked: false,
        disabled: false,
        id: 1,
        label: 'Regular',
        value: 'regular'
      },
      {
        checked: false,
        disabled: false,
        id: 2,
        label: 'Deep Dish',
        value: 'deep-dish'
      },
      {
        checked: false,
        disabled: false,
        id: 3,
        label: 'Thin Crust',
        value: 'thin-crust'
      },
      {
        checked: false,
        disabled: true,
        id: 4,
        label: 'Super Thin Crust',
        value: 'super-thin-crust'
      }
    ],
    selectedPizza: 'regular'
  });

  const [stateFour, setStateFour] = useState({
    selected: null
  });

  const handleChangeOne = e => setStateOne({ selectedValue: e.target.value });
  const handleChangeFour = e => setStateFour({ selected: e.target.value });
  const handleSubmitFour = () => {
    if (stateFour.selected) {
      setShowError(false);
    } else {
      setStateFour({
        selected: false
      });
      setShowError(true);
    }
    refOneValidation.current.focus();
  };

  return (
    <Grid>
      <Row>
        <Col>
          <Typography tag="h2">Radio default</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <RadioGroup>
            <RadioGroupLegend>Choose a shipping speed</RadioGroupLegend>
            <Radio
              name="radio-default-example"
              value="1"
              label="One-Day Shipping"
            />
            <Radio
              name="radio-default-example"
              value="2"
              label="Two-Day Shipping"
            />
          </RadioGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Radio (stateful) with preselection.</Typography>

          <Typography>Composition approach</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <RadioGroup>
            <RadioGroupLegend>Choose a shipping speed</RadioGroupLegend>
            <Radio
              defaultChecked
              name="radio-group-stateful-example"
              value="1"
              label="One-Day Shipping"
            />
            <Radio
              name="radio-group-stateful-example"
              value="2"
              label="Two-Day Shipping"
            />
          </RadioGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Radio (stateless) with preselection.</Typography>

          <Typography>Composition approach</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <RadioGroup>
            <RadioGroupLegend>Choose a shipping speed</RadioGroupLegend>
            <Radio
              checked={stateOne.selectedValue === '1'}
              label="One-Day Shipping"
              name="radio-group-stateless-preselection-example"
              onChange={handleChangeOne}
              value="1"
            />
            <Radio
              checked={stateOne.selectedValue === '2'}
              label="Two-Day Shipping"
              name="radio-group-stateless-preselection-example"
              onChange={handleChangeOne}
              value="2"
            />
          </RadioGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Radio Group Control (legacy wrapper)</Typography>

          <Typography>To create a custom wrapper around a radio group, see the RadioGroupControl component in the code base.</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <RadioGroupControl
            value={stateThree.selectedPizza}
            onChange={e => setStateThree({ ...stateThree, selectedPizza: e.target.value })}
            legend="Select Pizza Crust"
          >
            {
              stateThree.pizzas.map((pizza, k) => (
                <Radio
                  disabled={pizza.disabled}
                  key={`legacy-radio-group-pizza-${k}`}
                  id={`legacy-radio-group-control-pizza-${k}`}
                  label={pizza.label}
                  name="legacy-radio-control-pizza-radios"
                  value={pizza.value}
                />
              ))
            }
          </RadioGroupControl>

        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Radio Group with validation</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <RadioGroup invalid={showError}>
            <RadioGroupLegend>Select Pizza Crust</RadioGroupLegend>
            <RadioList>
              <RadioListItem>
                <Radio
                  invalid={!stateFour.selected && stateFour.selected !== null}
                  label="Regular"
                  name="radio-group-validation-example"
                  onChange={handleChangeFour}
                  ref={refOneValidation}
                  value="Regular"
                />
              </RadioListItem>
              <RadioListItem>
                <Radio
                  invalid={!stateFour.selected && stateFour.selected !== null}
                  label="Deep Dish"
                  name="radio-group-validation-example"
                  onChange={handleChangeFour}
                  ref={refTwoValidation}
                  value="Deep Dish"
                />
              </RadioListItem>
              <RadioListItem>
                <Radio
                  invalid={!stateFour.selected && stateFour.selected !== null}
                  label="Thin Crust"
                  name="radio-group-validation-example"
                  onChange={handleChangeFour}
                  ref={refThreeValidation}
                  value="Thin Crust"
                />
              </RadioListItem>
            </RadioList>
            {
              showError === true
                ? <RadioErrorMessage><Icon name="error" resolution="low" /> Error: Please select crust type.</RadioErrorMessage>
                : undefined
            }
          </RadioGroup>
          <ButtonText
            onClick={handleSubmitFour}
          >
            Submit (test validation)
          </ButtonText>
        </Col>
      </Row>
    </Grid>
  );
};

export default RadioExample;
